<template>
	<div class="articlesList diyscrollbar">
		<div class="article" v-for="a in articles">
			<el-badge v-bind:value="a.state==1?'未发表':''" class="item">
				<div class="a-title">{{a.title}}</div>
			</el-badge>
			<div class="a-author">
				作者&nbsp;:&nbsp;{{a.author}}
			</div>
			<div class="a-time">
				发布时间&nbsp;:&nbsp;{{a.time}}
			</div>
			<div class="a-views">
				浏览次数&nbsp;:&nbsp;{{a.views}}
			</div>
			<div class="a-content">
				内容摘要&nbsp;:&nbsp;{{a.content}}
			</div>
			<div class="a-tags">
				文章标签&nbsp;:&nbsp;<br />
				<span v-for="tag in a.tags">
					{{tag}}
				</span>
			</div>
			<div class="a-operation">
				<el-button v-on:click="read(a)" type="info" icon="view"></el-button>
				<el-button v-on:click="amend(a)" type="primary" icon="edit"></el-button>
				<el-button v-on:click="delArticle(a)" type="danger" icon="delete"></el-button>
			</div>
		</div>
	</div>
</template>

<script>
	import { Badge } from 'element-ui'
	export default {
		name: 'articlesList',
		props: ['articles'],
		components: {
			'el-badge': Badge,
		},
		data() {
			return {

			}
		},
		created() {

		},
		methods:{
			delArticle(article){
				this.$confirm('此操作将永久删除'+article.title+', 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					console.log('删除文章jjjjj');
					console.log(article.id);
					this.$http.get("url"+article.id).then((response) => {
						if(response.data.data){
							for(let i = 0; i < this.articlesList.length; i++) {
								if(this.articlesList[i].id == article.id) {
									this.articlesList.splice(i, 1);
									this.$message.success('删除成功');
								}
							}
							
						}else{
							this.$message.error('删除失败');
						}
					}, (response) => {
						this.$message.error('链接失败');
					});
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消删除'
					});
				});
			},
			read(a){
				this.$emit('read',a);
			},
			amend(a){
				this.$emit('amend',a);
			}
		},
	}
</script>

<style>
	.articlesList {
		position: relative;
		padding: 5px 10px;
		overflow: auto;
		height: calc(100% - 66px);
	}
	
	.article {
		float: left;
		position: relative;
		width: 250px;
		margin: 7px;
		border: 1px solid #ccc;
	}
	
	.a-title {
		font-size: 20px;
		padding: 7px;
		width: 236px;
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		border-bottom: 1px solid #ccc;
	}
	
	.a-author,
	.a-time,
	.a-views,
	.a-content,
	.a-tags {
		font-size: 13px;
		line-height: 20px;
		padding: 1px 5px;
	}
	
	.article .el-badge__content.is-fixed {
		z-index: 3;
		right: 30px;
	}
	
	.a-content{
		display: -webkit-box !important;
		overflow: hidden;
		text-overflow: ellipsis;
		word-break: break-all;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 3;
	}
	.a-tags {
		background-color: #fff;
	}
	
	.a-tags span {
		display: inline-block;
		font-size: 13px;
		border: 1px solid #20A0FF;
		padding: 2px 10px;
		margin: 1px 0;
		border-radius: 40px;
		cursor: pointer;
		color: #1D8CE0;
		max-width: 218px;
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
	}
	.a-operation{
		padding: 5px;
	}
	.a-operation .el-button {
		margin-left: 5px;
		padding: 7px;
	}
</style>


