<template>
	<div class="editMd">
		<el-input size="large" placeholder="标题" v-model="article.title">
		</el-input>
		<div class="labels">
			<el-form ref="dialog.form" :model="article" label-width="80px">
				<el-form-item label="文章标签">
					<el-tag :key="tag" v-for="tag in article.tags" :closable="true" :close-transition="false" @close="handleClose(tag)">
						{{tag}}
					</el-tag>
					<el-autocomplete v-model="dialog.form.inputTag" :fetch-suggestions="querySearchTag" placeholder="请输入标签" :trigger-on-focus="false" @select="handleSelect" ref="saveTagInput" @keyup.enter.native="handleInputConfirm" @blur="handleInputConfirm"></el-autocomplete>
				</el-form-item>
			</el-form>
		</div>
		<div id="editormd"></div>
		<div class="submit">
			<el-button @click="cancel">取消</el-button>
			<el-button type="primary" @click="save">存为草稿</el-button>
			<el-button type="primary" @click="submitArticle">提交</el-button>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'articleWrite',
		props: ['article'],
		components: {

		},
		data() {
			return {
				content: '',
				editorObj: {},
				dialog: {
					inputAddLabelShow: false,
					inputNewLabelValue: '',
					control: false, //控制dialog的开关
					form: {
						inputTag: ''
					},
					restaurants: [{
							"value": "javaScript"
						},
						{
							"value": "HTML"
						},
						{
							"value": "java"
						},
						{
							"value": "R語言"
						},
						{
							"value": "CSS"
						},
						{
							"value": "NodeJS"
						},
						{
							"value": "服务端"
						},
					],
				},

			}
		},
		methods: {
			cancel() {
				this.$emit('close');
			},
			save() {
				console.log("保存文章iiii");
					console.log({
						"title": this.article.title,
						"state": 0,
						"tags": this.article.tags,
						"mdContent": testEditor.getMarkdown(),
					});
					this.$http.post('/admin/article', {
						"title": this.article.title,
						"state": 1,
						"tags": this.article.tags,
						"mdContent": testEditor.getMarkdown(),
					}).then((response) => {
						//2.有结果返回后，返回的数据提交到state
						this.cancel();
						this.$message.success('提交成功');
					}, (response) => {
						this.$message.error('提交失败');
					});
			},
			submitArticle() {
				if(this.article.id) { //修改
					console.log("修改文章hhhh");
					console.log({
						"id": this.article.id,
						"title": this.article.title,
						"state": 1,
						"tags": this.article.tags,
						"mdContent": testEditor.getMarkdown(),
					});
					this.$http.post('/admin/article', {
						"id": this.article.id,
						"title": this.article.title,
						"state": 1,
						"tags": this.article.tags,
						"mdContent": testEditor.getMarkdown(),
					}).then((response) => {
						//2.有结果返回后，返回的数据提交到state
						this.cancel();
						this.$message.success('提交成功');
					}, (response) => {
						this.$message.error('提交失败');
					});
				} else { //新增
					console.log("新增文章ggg");
					console.log({
						"title": this.article.title,
						"state": 0,
						"tags": this.article.tags,
						"mdContent": testEditor.getMarkdown(),
					});
					this.$http.post('/admin/article', {
						"title": this.article.title,
						"state": 0,
						"tags": this.article.tags,
						"mdContent": testEditor.getMarkdown(),
					}).then((response) => {
						//2.有结果返回后，返回的数据提交到state
						this.cancel();
						this.$message.success('提交成功');
					}, (response) => {
						this.$message.error('提交失败');
					});
				}

			},
			handleClose(tag) { //删除显示的标签
				this.article.tags.splice(this.article.tags.indexOf(tag), 1);
			},
			handleInputConfirm() { //添加标签
				let inputTag = this.dialog.form.inputTag;
				if(inputTag) {
					this.article.tags.push(inputTag);
				}
				this.dialog.form.inputTag = '';
			},
			querySearchTag(queryString, callback) { //根据输入的查找标签
				var restaurants = this.dialog.restaurants;
				var results = queryString ? restaurants.filter(this.createFilterTag(queryString)) : restaurants;
				// 调用 callback 返回建议列表的数据
				callback(results);
			},
			createFilterTag(queryString) { //根据输入的标签过滤相关地1标签
				return(restaurant) => {
					return(restaurant.value.indexOf(queryString) === 0);
				};
			},
			handleSelect(item) { //点击筛选后的标签列表
				//console.log(item);
				this.handleInputConfirm();
			},
		},
		created() {
			createdEditormd('editormd'); //初始化Editor.md
			if(this.article.id) { //修改
				testEditor.settings.markdown = this.article.content;
			} else {
				testEditor.settings.markdown = '';
			}

		}
	}
</script>

<style>
	/*.input-new-tag {
		width: 100px;
	}
	
	.el-dialog .el-tag {
		display: inline-block;
		font-size: 14px;
		height: 36px;
		line-height: 34px;
		padding: 0 7px;
		margin-right: 10px;
	}*/
	
	.labels .el-form-item {
		margin: 0;
		padding: 5px 0;
		background-color: #f7f7f7;
		width: 100%;
	}
	
	.ql-editor {
		min-height: 500px;
	}
	
	.editMd .el-input__inner {
		border-radius: 0;
	}
	
	.editormd {
		/*border-top: none;*/
	}
	
	.submit {
		/*text-align: center;*/
	}
</style>


